import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  },
  {
    path: "/joblisting/main",
    name: "JobListingMain",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(
        /* webpackChunkName: "about" */ "@/components/JobListing/Main.vue"
      )
  },
  {
    path: "/countries/main",
    name: "CountriesMain",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(
        /* webpackChunkName: "about" */ "@/components/Countries/Main.vue"
      )
  },
  {
    path: "/countries/country/:slug",
    name: "Details",
    props: true,
    component: () =>
      import(/* webpackChunkName: "details" */ "@/components/Countries/Details.vue"),
    beforeRouteUpdate: (to) => {
        this.name = to.params.name
      }
  },
  {
    path: "/tacos/main",
    name: "TacosMain",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(
        /* webpackChunkName: "about" */ "@/components/Tacos/Main.vue"
      )
  },
  {
    path: "/tacos/details/:slug",
    name: "TacoDetails",
    props: true,
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(
        /* webpackChunkName: "about" */ "@/components/Tacos/Details.vue"
      ),
    beforeRouteUpdate: (to) => {
      this.name = to.params.name
    }
  }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if(savedPosition) {
      return savedPosition
    } else {
      const position = {};
      if (to.hash) {
        position.selector = to.hash;
        if(to.hash === '#experience') {
          position.offset = {y: 140}
        }
        if(document.querySelector(to.hash)) {
          return position;
        }
        return false;
      }
    }
  }
});

export default router;
