import data from "@/store/modules/data.json";

export default {
  state: {
    data: data,
    filterRole: null,
    filterLevel: null,
    filters: {
      language: [],
      tool: []
    },
    filterCount: 0
  },
  actions: {
    changeFilters(context, event) {
      let key = event.target.getAttribute("data-type");
      let value = event.target.getAttribute("data-value") || event.target.value;
      let action = event.target.getAttribute("data-action");
      let pair = { key: key, value: value };
      if (action === "add") {
        if (key == "role" || key == "level") {
          context.commit("addFilterItem", pair);
        } else {
          let filterItem = context.state.filters[key].indexOf(value);
          if (filterItem == -1) {
            context.commit("addFilterItem", pair);
          }
        }
      } else {
        context.commit("removeFilterItem", pair);
      }
    }
  },
  getters: {
    jobList(state) {
      let list = state.data;
      if (state.filterRole != null) {
        list = list.filter(item => {
          return item.role == state.filterRole;
        });
      }
      if (state.filterLevel != null) {
        list = list.filter(item => {
          return item.level == state.filterLevel;
        });
      }
      if (state.filters.language.length > 0) {
        for (var i = 0; i < state.filters.language.length; i++) {
          list = list.filter(item => {
            return item.languages.includes(state.filters.language[i]);
          });
        }
      }
      if (state.filters.tool.length > 0) {
        for (var j = 0; j < state.filters.tool.length; j++) {
          list = list.filter(item => {
            return item.tools.includes(state.filters.tool[j]);
          });
        }
      }
      return list;
    },
    getFilters(state) {
      let roleArr = [];
      let levelArr = [];
      let langArr = [];
      let toolArr = [];
      state.data.map(item => {
        if (roleArr.indexOf(item.role) == -1) {
          roleArr.push(item.role);
        }
        if (levelArr.indexOf(item.level) == -1) {
          levelArr.push(item.level);
        }
        item.languages.map(lang => {
          if (langArr.indexOf(lang) == -1) {
            langArr.push(lang);
          }
        });
        item.tools.map(tool => {
          if (toolArr.indexOf(tool) == -1) {
            toolArr.push(tool);
          }
        });
      });
      roleArr = roleArr.sort();
      levelArr = levelArr.sort();
      langArr = langArr.sort();
      toolArr = toolArr.sort();
      let filters = {
        roles: roleArr,
        levels: levelArr,
        languages: langArr,
        tools: toolArr
      };
      return filters;
    },
    selectedFilters(state) {
      let filterArray = [];
      if (state.filterRole != null) {
        filterArray.push({ category: "role", value: state.filterRole });
      }
      if (state.filterLevel != null) {
        filterArray.push({ category: "level", value: state.filterLevel });
      }
      state.filters.language.map(item =>
        filterArray.push({ category: "language", value: item })
      );
      state.filters.tool.map(item =>
        filterArray.push({ category: "tool", value: item })
      );
      return filterArray;
    }
  },
  mutations: {
    addFilterItem(state, pair) {
      if (pair.key == "role") {
        state.filterRole = pair.value;
      } else if (pair.key == "level") {
        state.filterLevel = pair.value;
      } else {
        state.filters[pair.key].push(pair.value);
      }
      state.filterCount++;
    },
    removeFilterItem(state, pair) {
      if (pair.key != "role" && pair.key != "level") {
        var index = state.filters[pair.key].indexOf(pair.value);
        if (index != -1) {
          state.filters[pair.key].splice(index, 1);
          state.filterCount--;
        }
      }
      if (pair.key == "role") {
        state.filterRole = null;
      }
      if (pair.key == "level") {
        state.filterLevel = null;
      }
    }
  }
};
