import axios from "axios";

export default {
    state: {
        location: '',
        term: 'tacos',
        locations: [],
        offset: 0,
        currentBusiness: {},
        sortby: 'best_match',
        isLoading: false
    },
    getters: {
        getLocations(state) {
            return state.locations
        },
        getLocation(state) {
            return state.location
        },
        getCurrentBusiness(state) {
            return state.currentBusiness
        },
        getSortBy(state) {
            return state.sortby
        },
        getOffset(state) {
            return state.offset
        },
        getLoadingStatus(state) {
            return state.isLoading
        }
    },
    actions: {
        fetchLocations({commit, state}) {
            commit('changeStatus')
            axios.get(`https://colorful-halibut.glitch.me/api/v1/businesses/search?location=${state.location}&term=${state.term}&offset=${state.offset}&sort_by=${state.sortby}`)
            .then(response => {
                commit('updateLocations', response.data.businesses)
            }).catch(error => {
                console.log(error)
            });
        },
        fetchBusiness({commit}, id) {
            axios.get(`https://colorful-halibut.glitch.me/api/v1/businesses/${id}`)
            .then(response=>{
                commit('setBusiness', response.data)
            }).catch(error=>{
                console.log(error)
            })
        },
        fetchNewlocations({commit, state}) {
            console.log(`https://colorful-halibut.glitch.me/api/v1/businesses/search?location=${state.location}&term=${state.term}&offset=${state.offset}&sort_by=${state.sortby}`)
            axios.get(`https://colorful-halibut.glitch.me/api/v1/businesses/search?location=${state.location}&term=${state.term}&offset=${state.offset}&sort_by=${state.sortby}`)
                .then(response => {
                    commit('addNewLocations', response.data.businesses)
                }).catch(error => {
                    console.log(error)
                });
        }
    },
    mutations: {
        updateLocations(state, businesses) {
            state.currentBusiness = {}
            state.locations = businesses
            let newstate = !state.isLoading
            state.isLoading = newstate
        },
        setLocation(state, location) {
            state.location = location
        },
        setSortBy(state, sortby) {
            state.sortby = sortby
        },
        setBusiness(state, business) {
            state.currentBusiness = business
        },
        setOffset(state, offset) {
            state.offset = offset
        },
        addNewLocations(state, businessess) {
            let newArr = state.locations.concat(businessess)
            state.locations = newArr
        },
        changeStatus(state) {
            let newstate = !state.isLoading
            state.isLoading = newstate
        }
    }
}