import axios from "axios";

export default {
    state: {
      countryList: [],
      countryName: "",
      currentCountry: "",
      regionName: "", 
      isDark: false
    },
    getters: {
      getCountry(state) {
        return state.currentCountry;
      },
      getCountries(state) {
        if(state.countryName != '') {
          return state.countryList.filter((item) => {return item.name.normalize("NFD").replace(/[\u0300-\u036f]/g, "").toLowerCase().includes(state.countryName.toLowerCase())})
        } else if (state.regionName != '') {
          return state.countryList.filter(item=> {return item.region === state.regionName})
        } else {
          return state.countryList
        }      
      },
      getCountryName(state) {
        return state.countryName
      },
      getRegionFilters(state) {
        let filters = []
        state.countryList.map(item => {
          if (filters.indexOf(item.region) == -1) {
            filters.push(item.region);
          }
        });
        return filters.sort();
      },
      getRegionName(state) {
        return state.regionName
      },
      isDark(state) {
          return state.isDark
      }
    },
    actions: {
      fetchCountries({commit}) {
          axios.get("https://restcountries.eu/rest/v2/all")
            .then(response =>{
              commit('setCountryList', response.data)
            })
            .catch(error => {
              console.log(error)
            });
      },
      fetchCountry ({commit}, slug) {
        axios.get("https://restcountries.eu/rest/v2/alpha/" + slug)
            .then(response =>{
              commit('setCountry', response.data)
            })
            .catch(error => {
              console.log(error)
            });
      }
    },
    mutations: {
      setCountryList(state, countries) {
        state.countryList = countries;
      },
      setCountry(state, country) {
        state.currentCountry = country;
      },
      setBorderCountries(state, arr) {
        state.borderCountries = arr
      },
      setCountryName(state, input) {
        state.countryName = input
      },
      setRegionName(state, region) {
        state.regionName = region
      },
      setIsDark(state, status) {
          state.isDark = status
      }
    }
  };
  